<template>
  <div id="app">
    <router-view v-if="isRouterAlive"/>
  </div>
</template>


<script>
    export default {
    	provide (){
    		return {
    			reload:this.reload
    		}
    	},
    	data(){
    		return{
    			isRouterAlive: true
    		}
    	},
    	methods:{
    		reload(){
    			this.isRouterAlive = false;
    			this.$nextTick(()=>{
    				this.isRouterAlive = true;
    			})
    		}
    	}
    }
</script>
<style lang="scss">
	@import './assets/css/reset';
	@import './assets/css/common';
	
</style>



